    border-top: 0; }
  .card-foldable.desktop-disable .card-body {
    background-color: transparent; }
  .card-foldable.desktop-disable .card-body > p:first-child {
    margin-top: 0; }
  .card-foldable.desktop-disable .collapse {
    display: block;
    height: auto !important;
    visibility: visible; }
  .card-foldable.desktop-disable .collapse:not(.show) {
    display: block; }
  .card-foldable.desktop-disable .collapsing {
    position: relative;
    height: unset !important;
    overflow: hidden; } }

