.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto; }

/*--------------------------------------------------------------
# Global Header
--------------------------------------------------------------*/
nav.navbar {
  border-bottom: 1px solid #cccccc;
  padding-bottom: 0; }

.navbar-brand img {
  height: 80px; }

.navbar-nav .active {
  border-bottom: 5px solid #ffc627; }

.title span {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0.5rem; }

.title h1 {
  font-size: 1.5rem;
  margin-top: 0; }

/* Breakpoint at which the mobile menu is triggered */
@media (max-width: 991.98px) {
  .navbar {
    padding: 0; }
  .navbar > .container {
    margin-left: 0;
    margin-right: 0;
    width: 100%; }
  .navbar-brand img {
    content: url("https://www.asu.edu/asuthemes/4.9/assets/arizona-state-university-logo.png");
    height: 32px; }
  .title {
    font-size: 1rem;
    line-height: -1rem; }
  .title span {
    display: none; }
  .nav-item {
    border-bottom: 1px solid #cccccc; } }
